.container {
  max-width: 902px;
  margin: 0 auto;
  padding: 0 15px;
}

* {
  margin: 0;
  border: 0;
  padding: 0;
}

*,
*::before,
*::after {
  box-sizing: border-box;
  -webkit-box-sizing: border-box;
  -moz-box-sizing: border-box;
}

:focus,
:active {
  outline: none;
}

a:focus,
a:active {
  outline: none;
}

nav,
footer,
header,
aside {
  display: block;
}

html,
body {
  width: 100%;
  height: 100%;
  font-family: "JJannon";
  font-size: 100%;
  line-height: 1;
  -ms-text-size-adjust: 100%;
  -moz-text-size-adjust: 100%;
  -webkit-text-size-adjust: 100%;
}

input,
button,
textarea,
h1,
h2,
h3,
h4,
h5,
h6 {
  font-family: "JJannon";
}

input::-ms-clear {
  display: none;
}

button {
  cursor: pointer;
}

button::-moz-focus-inner {
  border: 0;
  padding: 0;
}

a,
a:visited {
  text-decoration: none;
}

a:hover {
  text-decoration: none;
}

ul li {
  list-style: none;
}

img {
  vertical-align: top;
  max-width: 100%;
}

.home-page {
  padding: 104px 0 192px 0;
}

.home-page .home-page__banner {
  max-width: 872px;
  height: 350px;
  margin-bottom: 88px;
}

.home-page .home-page__banner img {
  object-fit: contain;
  height: 100%;
}

.home-page .home-page__content {
  position: relative;
  padding-left: 25.05543%;
}

@media (max-width: 576px) {
  .home-page .home-page__content {
    padding: 0;
  }
}

.home-page .home-page__content .home-page__title {
  margin-bottom: 44px;
}

.home-page .home-page__content .home-page__title h2, .home-page .home-page__content .home-page__title h3 {
  font-size: 40px;
  font-weight: 400;
  font-family: "Mabry Pro";
  line-height: 120%;
  color: #4D7EC3;
  text-transform: uppercase;
}

.home-page .home-page__content .home-page__authors {
  width: 100%;
}

@media (min-width: 576px) {
  .home-page .home-page__content .home-page__authors {
    position: absolute;
    top: 200px;
    left: 0;
    max-width: 132px;
  }
}

.home-page .home-page__content .home-page__authors .home-page__authors-item {
  font-family: "JJannon";
  font-weight: 700;
  font-size: 16px;
  line-height: 125%;
  color: #4D7EC3;
  margin-bottom: 16px;
}

@media (max-width: 576px) {
  .home-page .home-page__content .home-page__authors .home-page__authors-item {
    display: flex;
  }
}

.home-page .home-page__content .home-page__authors .home-page__authors-item .home-page__authors-title {
  font-family: "Mabry Pro";
  font-weight: 400;
  font-size: 10px;
  line-height: 16px;
  text-transform: uppercase;
  color: #505050;
}

@media (max-width: 576px) {
  .home-page .home-page__content .home-page__authors .home-page__authors-item .home-page__authors-title {
    font-family: "JJannon";
    font-size: 15px;
    color: #4D7EC3;
    text-transform: none;
  }
}

.home-page .home-page__content .home-page__authors .home-page__authors-item .home-page__authors-name {
  font-weight: 800;
}

.home-page .home-page__content .home-page__authors .home-page__authors-item .home-page__authors-employment {
  font-weight: 400;
  font-style: italic;
}

.home-page .home-page__content .home-page__text {
  margin-bottom: 64px;
}

.home-page .home-page__content .home-page__cite {
  border-top: 2px solid #27A46A;
  border-bottom: 2px solid #27A46A;
  padding: 64px 0;
  margin-bottom: 64px;
}

.home-page .home-page__content .home-page__cite p {
  font-weight: 400;
  font-size: 32px;
  line-height: 130%;
  color: #27A46A;
  margin-bottom: 30px;
}

.home-page .home-page__content .home-page__cite p span {
  font-style: italic;
}

.home-page .home-page__content .home-page__image {
  max-width: 648px;
  height: 399px;
  margin-bottom: 64px;
}

.home-page .home-page__content .home-page__image img {
  object-fit: contain;
  height: 100%;
}

.home-page .home-page__content .home-page__list .home-page__item {
  margin-bottom: 64px;
}

.home-page .home-page__content .home-page__list .home-page__item .home-page__item-body {
  padding-left: 40px;
}

.home-page .home-page__content .home-page__list .home-page__item .home-page__item-body .home-page__subtitle {
  margin-bottom: 24px;
}

.home-page .home-page__content .home-page__list .home-page__item .home-page__item-body .home-page__subtitle h3 {
  font-size: 24px;
}

.home-page .home-page__content .home-page__list .home-page__item .home-page__item-body .home-page__link {
  display: inline-block;
  font-family: "JJannon";
  font-size: 24px;
  font-weight: 700;
  color: #4D7EC3;
  margin-top: 24px;
}

.home-page .home-page__content .home-page__list .home-page__sub-item {
  margin-top: 64px;
}

.home-page .home-page__button {
  display: inline-block;
  font-size: 18px;
  height: 44px;
  line-height: 44px;
  text-transform: uppercase;
  padding-left: 80px;
  padding-right: 40px;
  color: #FFFFFF;
  background: #4D7EC3;
  border-radius: 30px;
}

@media (max-width: 425px) {
  .home-page .home-page__button {
    display: block;
  }
}

.home-page .home-page__button span {
  padding-right: 40px;
  background: url("../img/arror.svg") right no-repeat;
}

.text-content p {
  font-family: "JJannon";
  font-size: 24px;
  font-weight: 400;
  line-height: 130%;
  color: #4D7EC3;
}

.text-content p:not(:last-child) {
  margin-bottom: 20px;
}
